.form {
    display: flex;
    align-items: center;
    margin: 0 auto;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 48px;
    border: 0;
    /* background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg'); */

    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    background-color: black;
    color: rgb(142, 142, 142);
    cursor: pointer;
    outline: none;
}

.button:hover {
    color: #fff;
    opacity: 1;
}


.input {
    display: inline-block;
    height: 42px;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: 2px solid whitesmoke;
    outline: none;
    padding-left: 15px;
    padding-right: 4px;
}

.input::placeholder {
    font-size: 20px;
}
li{
    list-style: none;
}